import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

import { Grid } from "@material-ui/core"
import ProjectBox from "../components/projectbox"
import { graphql } from 'gatsby'

export default function ProjectsPage({ data }) {
	const documents = data.allMarkdownRemark.edges;
	console.log(documents);
	return(
		<Layout>
			<SEO title="Projects" />
			<div style={{padding: 15}}>
				<div style={{opacity: 0.666}}><i>"...creation is embarrassing. For every new good idea you have, there are a hundred, ten thousand foolish ones, which you naturally do not care to display."</i></div>
				<div style={{textAlign: 'right', color: 'black', opacity: 0.8}}><a href="https://www.technologyreview.com/2014/10/20/169899/isaac-asimov-asks-how-do-people-get-new-ideas/" style={{color: 'black'}}>- Isaac Asimov</a></div>
			</div>
			<div style={{marginBottom: 40}}>
				<h2>Featured Projects</h2>
				<Grid container spacing={2}>
					{
						documents.map(({ node }) => {
							if(node.frontmatter.type === "project" && node.frontmatter.featured) {
								const title = node.frontmatter.title || node.fields.slug
								return(
									<ProjectBox 
										key={node.fields.slug}
										title={title}
										summary={node.frontmatter.summary}
										path={node.fields.slug}
										image={node.frontmatter.image.childImageSharp.fluid}
								
									/>
								)
							} else {
								return null;
							}
						})
					}
				</Grid>
			</div>
			
			<div style={{marginBottom: 40}}>
				<h2>All Projects</h2>
				<Grid container spacing={2}>
					{
						documents.map(({ node }) => {
							if(node.frontmatter.type === "project") {
								const title = node.frontmatter.title || node.fields.slug
								return(
									<ProjectBox 
										key={node.fields.slug}
										title={title}
										summary={node.frontmatter.summary}
										path={node.fields.slug}
										image={node.frontmatter.image.childImageSharp.fluid}
								
									/>
								)
							} else {
								return null;
							}
						})
					}
				</Grid>
			</div>
			
		</Layout>
	)
}

export const projectQuery = graphql`
  query projectQuery {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
            id
            fields {
                slug
            }
            frontmatter {
                title
				date(formatString: "MMMM DD, YYYY")
				type
				featured
				summary
                image {
					id
					childImageSharp {
						fluid(quality: 90, maxWidth: 1920) {
						  ...GatsbyImageSharpFluid_withWebp
						}
					  }
				  
				}
            }
            excerpt
        }
      }
    }
  }
`